* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  font-family: Arial, sans-serif;
  display: flex;
}

#editor {
  border: 1px solid #ddd;
  border-radius: 4px;
  width: 100%;
  height: 900px;
  padding: 0;
  position: relative;
}

#scene {
  width: calc(100vw - 600px);
  height: 100vh;
}

#control {
  background-color: #f2f2f2;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 10px;
  width: 600px;
  height: 100vh;
  margin: 0 10px;
  padding: 20px;
  display: flex;
  box-shadow: 0 4px 8px #0000001a;
}

.button-container {
  justify-content: space-around;
  width: 100%;
  display: flex;
}

button {
  color: #fff;
  text-align: center;
  cursor: pointer;
  background-color: #4caf50;
  border: 2px solid #4caf50;
  border-radius: 5px;
  margin: 4px 2px;
  padding: 10px 20px;
  font-size: 12px;
  text-decoration: none;
  transition-duration: .4s;
  display: inline-block;
}

button:hover {
  color: #000;
  background-color: #fff;
  border: 2px solid #4caf50;
}

#speedControl {
  width: 100%;
  margin: 15px 0;
}

